.page-header {
    background-color: $color-violet-decorative;
    background-image: url(../img/header-bg.png);
    background-size: 100%;
    background-repeat: no-repeat;
    background-size: cover;
}

.page-header__wrapper {
    margin: 0 auto;
    width: 1170px;
    padding: 0 15px;
}

.page-header__top {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding-top: 67px;
    padding-bottom: 20px;
}

.page-header__middle {
    background-image: url(../img/header-middle-bg-desktop.png);
    background-repeat: no-repeat;
    background-position: -22% 40%;
    padding-top: 111px;
    padding-bottom: 77px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.page-header__bottom {
    padding-top: 40px;
    padding-bottom: 67px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.page-header__scroll {
    display: inline-block;
    width: 19px;
    height: 31px;
    background-image: url(../../img/scroll.png);
    background-size: 19px auto;
    background-position: center;
    &:focus,
    &:hover {
        transform: scale(1.5);
    }
}

@media (max-width: $tablet-width) {
    .page-header__wrapper {
        width: 100%;
        padding: 0;
    }
    .page-header__top {
        padding-top: 26px;
    }
    .page-header__middle {
        background-image: url(../img/header-middle-bg-tablet.png);
        background-position: 1%;
        padding-top: 45px;
        padding-bottom: 0px;
    }
    .page-header__bottom {
        padding-top: 0px;
        padding-bottom: 36px;
    }
}
